import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { publishReplay, refCount } from 'rxjs/operators';
import { Product, ShoppingCart } from './model';

@Injectable({
  providedIn: 'root'
})
export class WebshopService {
  private api = 'api';
  private products: Observable<Product[]>;
  private promotion: Observable<{ value: any , Action: any }>;
  constructor(private http: HttpClient) { }

  getProducts(): Observable<Product[]> {
    if (!this.products) {
      this.products = (this.http.get(`${this.api}/products`) as Observable<Product[]>).pipe(
        publishReplay(),
        refCount()
      );
    }
    return this.products;
  }

  createSalesOrder(shoppingCart: ShoppingCart): Observable<any> {
    return this.http.post<ShoppingCart>(`${this.api}/sales-order`, {
      lineItems: shoppingCart.lineItems.map(li => ({
        quantity: li.quantity,
        productId: li.product.id
      }))
    }, {
      headers: { 'Content-Type': 'application/json' }
    });
  }

  getPromotion(): Observable<{ value: any, Action: any }> {
    if (!this.promotion) {
      this.promotion = this.http.get<any>(`${this.api}/promotion`);
    }
    return this.promotion;
  }
}
