import { ActivatedRouteSnapshot, DetachedRouteHandle, RouteReuseStrategy } from '@angular/router';
import { ShoppingCartComponent } from './shopping-cart/shopping-cart.component';

export class RoutingReuse extends RouteReuseStrategy {
  storage: {[key: string]: DetachedRouteHandle} = {};

  shouldDetach(route: ActivatedRouteSnapshot): boolean {
    return route.component !== ShoppingCartComponent;
  }

  store(route: ActivatedRouteSnapshot, handle: DetachedRouteHandle): void {
    this.storage[route.toString()] = handle;
  }

  shouldAttach(route: ActivatedRouteSnapshot): boolean {
    return !!this.storage[route.toString()];
  }

  retrieve(route: ActivatedRouteSnapshot): DetachedRouteHandle {
    return this.storage[route.toString()];
  }

  shouldReuseRoute(future: ActivatedRouteSnapshot, curr: ActivatedRouteSnapshot): boolean {
    return future.routeConfig === curr.routeConfig;
  }

}
