import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { Product } from '../model';
import { ShoppingCartService } from '../shopping-cart.service';
import { WebshopService } from '../webshop.service';
import { imageById } from './product-image-mapping';

@Component({
  selector: 'app-products-catalog',
  templateUrl: './products-catalog.component.html',
  styleUrls: ['./products-catalog.component.css']
})
export class ProductsCatalogComponent implements OnInit {
  products: Observable<Product[]>;

  constructor(private webshopService: WebshopService, private shoppingCartService: ShoppingCartService) { }

  ngOnInit(): void {
    this.products = this.webshopService.getProducts();
  }

  addToShoppingCart(product: Product, quantity: string): void {
    this.shoppingCartService.addProduct(product, parseInt(quantity, 10));
  }

  formatAmount(price: number): string {
    return `${Number(price).toFixed(2)} USD`;
  }

  productImgSrc(product: Product): string {
    return `../../assets/products/${imageById(product.id)}`;
  }
}
